header {
  height: 70px;
  padding: 20px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 4px 20px 16px 0px rgba(0, 0, 0, 0.1);
}

.nav_logo button {
  background: none;
  border: none;
  color: white;
  font-size: 30px;
  cursor: pointer;
  padding: 10px;
  border-radius: 10px;
  transition: all 0.5s;
}
.nav_logo button:hover {
  transform: scale(1.1);
}
.pages_btn {
  display: flex;
  gap: 0 20px;
}

.pages_btn button {
  background: none;
  border: none;
  color: white;
  font-size: 30px;
  cursor: pointer;
  padding: 10px;
  border-radius: 10px;
  transition: all 0.5s;
}

.pages_btn button:hover {
  background-color: var(--secondary-color);
  transform: scale(1.1);
}

.cv_btn button {
  background: none;
  border: none;
  color: white;
  font-size: 20px;
  cursor: pointer;
  padding: 10px;
  border-radius: 10px;
  transition: all 0.5s;
}

.cv_btn button:hover {
    background-color: var(--secondary-color);
    transform: scale(1.1);
  }